.nossas-solucoes {
    padding-top: 130px;
    padding-bottom: 110px;
    max-width: 100vw;
    overflow: hidden;
    @media screen and (max-width: $bp-MD) {
        padding: 50px 0;
    }
    @media screen and (min-width: 2000px) {
        padding-top: 210px;
        padding-bottom: 180px;
    }

    .container {
        max-width: 1450px;
        @media screen and (min-width: 2000px) {
            max-width: 2000px;
        }
    }

    &__title {
        font-family: 'FH';
        text-transform: uppercase;
        color: $color-secondary;
        font-size: 28px;
        letter-spacing: 4px;
        margin-right: -60px;
        line-height: 1.4;
        margin-top: 0;
        @media screen and (min-width: 1000px) and (max-width: 1300px) {
            font-size: 25px;
            margin-top: 14px;
        }
        @media screen and (min-width: $bp-MD) and (max-width: $bp-LG) {
            font-size: 39px !important;
            line-height: 1.6;
            padding-left: 30px;
            margin-bottom: 110px;
        }
        @media screen and (max-width: $bp-MD) {
            font-size: 27px;
            line-height: 1.6;
            padding-left: 30px;
            margin-bottom: 110px;
        }
        @media screen and (min-width: 390px) and (max-width: $bp-SM) {
            font-size: 30px;
            padding-left: 36px;
        }
        @media screen and (min-width: 2000px) {
            font-size: 37px;
            margin-top: -20px;
        }

        .stroke-lettering {
            letter-spacing: 6px;
        }

        .has-line {
            display: inline;
            position: relative;
            @keyframes slideIn110p {
                0% {
                    max-width: 0;
                }
                100% {
                    max-width: 130%;
                }
            }
            @keyframes slideInv2 {
                0% {
                    max-height: 0;
                }
                100% {
                    max-height: 140px;
                }
            }

            .line {
                position: absolute;
                width: calc(100% + 12px);
                height: 3px;
                background-color: $color-primary;
                z-index: -1;
                top: 50%;
                left: 4px;
                transform: translateY(-50%);
                max-width: 0;
                animation-fill-mode: forwards;
                @media screen and (min-width: 2000px) {
                    width: calc(100% + 50px);
                }
                @media screen and (min-width: $bp-LG) {
                    animation: slideIn110p 1.4s;
                    animation-fill-mode: forwards;
                }
                @media screen and (min-width: $bp-MD) and (max-width: $bp-LG) {
                    position: absolute;
                    bottom: -0px;
                    transform: none;
                    right: 83.7px !important;
                    left: unset;
                    width: 4px !important;
                    height: 140px !important;
                    max-width: unset;
                    max-height: 0;
                    animation: slideInv2 1.4s;
                    animation-fill-mode: forwards;
                }
                @media screen and (max-width: $bp-MD) {
                    position: absolute;
                    bottom: -0px;
                    transform: none;
                    right: 61.7px;
                    left: unset;
                    width: 3.1px;
                    height: 100px;
                    max-width: unset;
                    max-height: 0;
                    animation: slideInv2 1.4s;
                    animation-fill-mode: forwards;
                }
                @media screen and (min-width: 390px) {
                    right: 67px;
                }
                &:after {
                    content: '';
                    position: absolute;
                    top: 50%;
                    right: 0;
                    transform: translateY(-50%);
                    width: 8px;
                    height: 8px;
                    border-radius: 50%;
                    background-color: $color-primary;
                    @media screen and (max-width: $bp-LG) {
                        top: unset;
                        bottom: 0;
                        left: 50%;
                        transform: translateX(-50%);
                    }
                }
            }
        }

    }

    &__list {
        display: flex;
        justify-content: space-between;
        margin-left: -30px;
        @media screen and (max-width: $bp-LG) {
            flex-direction: column;
        }
    }

    &__item {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;

        @media screen and (max-width: $bp-LG) {
            &:not(:last-child), &:not(.last){
                margin-bottom: 90px;
            }
        }

        &:not(:last-child), &.has-plus {
            &::after {
                content: '';
                position: absolute;
                top: 101px;
                transform: translate(50%, -50%);
                right: -10px;
                background-image: url("../images/ico/plus.png");
                background-size: contain;
                object-position: center center;
                width: 16px;
                height: 16px;

                @media screen and (min-width: 2000px) {
                    width: 19px;
                    height: 19px;
                    right: -60px;
                }

                @media screen and (max-width: $bp-LG) {
                    top: unset;
                    right: unset;
                    left: 50%;
                    transform: translateX(-50%);
                    bottom: -58px;
                }
            }
        }

        &
        &:first-child{
            &::after {
                right: 4px;

                @media screen and (min-width: 1200px) and (max-width: 1400px){
                    right: -6px;
                }

                @media screen and (min-width: 2000px) {
                    right: -50px;
                }
            }
        }

        &:nth-child(2){
            &::after {
                right: -18px;

                @media screen and (min-width: 1200px) and (max-width: 1400px){
                    right: -25px;
                }

                @media screen and (min-width: 2000px) {
                    right: -57px;
                }
            }
        }

        &:nth-child(4){
            &::after{
                @media screen and (min-width: 1200px) and (max-width: 1400px){
                    right: -24px;
                }
            }
        }

        .img {
            width: 69px;
            height: 69px;
            object-fit: contain;
            object-position: center center;
            margin-bottom: 15px;
            @media screen and (min-width: $bp-MD) and (max-width: $bp-LG) {
                width: 110px;
                height: 110px;
            }
            @media screen and (min-width: 2000px) {
                width: 76px;
                height: 76px;
            }
        }

        .title {
            font-family: 'FB';
            font-size: 16px;
            color: $color-secondary;
            text-transform: uppercase;
            text-align: center;
            max-width: 200px;
            @media screen and (min-width: $bp-MD) and (max-width: $bp-LG) {
                font-size: 30px;
                max-width: 280px;
            }
            @media screen and (min-width: 2000px) {
                font-size: 19px;
            }
        }
        @keyframes slideInV40 {
            0% {
                max-height: 0;
            }
            100% {
                max-height: 44px;
            }
        }

        .line {
            position: relative;
            height: 44px;
            width: 2.4px;
            background-color: black;
            margin-top: 9px;
            margin-bottom: 17px;
            max-height: 0;
            animation: slideInV40 1s;
            animation-fill-mode: forwards;
            &:after {
                content: '';
                position: absolute;
                bottom: 0;
                left: 50%;
                transform: translateX(-50%);
                width: 8px;
                height: 8px;
                background-color: black;
                border-radius: 50%;
            }
        }

        .desc {
            font-family: 'FM';
            text-align: center;
            font-size: 11px;
            letter-spacing: 1px;
            margin: 0 auto;
            white-space: nowrap;
            line-height: 1.2;
            @media screen and (min-width: $bp-MD) and (max-width: $bp-LG) {
                font-size: 23px !important;
            }
            @media screen and (max-width: $bp-MD) {
                font-size: 14px;
            }
            @media screen and (min-width: 2000px) {
                font-size: 14px;
                letter-spacing: 1.5px;
            }
        }

        .link {
            display: block;
            text-align: right;
            width: 100%;
            color: $color-secondary;
            padding-right: 25px;
            text-transform: uppercase;
            font-family: 'FB';
            font-size: 12px;
            letter-spacing: 2.7px;
            margin-top: 5px;
            @media screen and (min-width: 1200px) {
                font-size: 12px;
            }
            @media screen and (min-width: $bp-MD) and (max-width: $bp-LG) {
                font-size: 20px;
                margin-top: 8px;
            }
        }
    }
}
