.banner-beneficios {
    position: relative;
    background-color: $color-secondary;
    margin-top: 100px;
    padding: 60px 0 10px 0;

    @media screen and (max-width: $bp-SM){
        padding: 30px 0 10px;
        margin-top: 20px;
    }

    .img-top {
        position: absolute;
        left: 0;
        top: 2px;
        width: 100%;
        transform: translateY(-100%);
    }

    &__caption {
        
        .title{
            font-size: 37px;
            font-family: 'FB';
            color: #fff;
            text-transform: uppercase;
            line-height: 1.5;
            letter-spacing: 6px;
            padding-left: 70px;

            @media screen and (max-width: 1300px){
                font-size: 34px;
            }
            
            @media screen and (max-width: 1200px){
                font-size: 30px;
                padding-left: 0;
            }

            @media screen and (max-width: $bp-LG){
                font-size: 32px;
                margin-bottom: 30px;
            }

            @media screen and (max-width: $bp-MD){
                font-size: 26px;
                padding-left: 0 !important;
            }
        }

        .has-line {
            position: relative;
            display: inline;

            @keyframes slideIn200px {
                0% {
                    max-width: 0;
                }
                100% {
                    max-width: 100px;
                }
            }

            .text {
                position: relative;
                display: inline;
                z-index: 4;
            }

            .line {
                position: absolute;
                width: 60px;
                height: 3px;
                max-width: 0;
                background-color: $color-primary;
                z-index: 1;
                top: 50%;
                right: 10px;
                transform: translate(100%, -50%);
                animation: slideIn200px 1s;
                animation-fill-mode: forwards;
                z-index: 2;

                &:after {
                    content: '';
                    position: absolute;
                    top: 50%;
                    right: 0;
                    transform: translateY(-50%);
                    width: 8px;
                    height: 8px;
                    border-radius: 50%;
                    background-color: $color-primary;
                }
            }
        }
    }

    &__item{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        color: #fff;
        text-align: center;
        margin-bottom: 50px;

        @media screen and (max-width: $bp-SM){
            margin-bottom: 20px;
        }

        @keyframes slideInV60 {
            0% {
                max-height: 0;
            }
            100% {
                max-height: 54px;
            }
        }

        .ico{
            width: 80px;
            height: 80px;
            object-fit: contain;
            margin: 0 auto;
            margin-bottom: 13px;

            &.lg{
                width: 100px;
            }

            @media screen and (max-width: $bp-SM){
                width: 60px;
                height: 70px;

                &.lg{
                    width: 80px;
                }
            }
        }

        .title{
            font-family: 'FB';
            max-width: 200px;
            font-size: 17px;
            line-height: 1.4;
            text-transform: uppercase;
            letter-spacing: 3px;
            

            @media screen and (max-width: $bp-SM){
                font-size: 16px;
            }
        }

        .desc{
            font-family: 'FO';
            font-weight: lighter;
            font-size: 13px;
            letter-spacing: 1px;

            @media screen and (max-width: $bp-SM){
                font-size: 12px;
            }
        }

        .line {
            position: relative;
            height: 60px;
            width: 2.8px;
            background-color: $color-primary;
            margin-top: 9px;
            margin-bottom: 17px;
            max-height: 0;
            animation: slideInV60 1s;
            animation-fill-mode: forwards;

            @media screen and (max-width: $bp-SM){
                height: 40px;
            }

            &:after {
                content: '';
                position: absolute;
                bottom: 0;
                left: 50%;
                transform: translateX(-50%);
                width: 8px;
                height: 8px;
                background-color: $color-primary;
                border-radius: 50%;
            }
        }
    }

    &.v-2{
        .banner-beneficios__caption{
            .title{
                padding-left: 40px;
                letter-spacing: 5px;
            }

            @keyframes slideIn300px {
                0% {
                    max-width: 0;
                }
                100% {
                    max-width: 300px;
                }
            }

            .has-line{
                .line{
                    right: 14px;
                    animation: slideIn300px 1s;
                    animation-fill-mode: forwards;
                    width: 240px;

                    @media screen and (max-width: $bp-MD){
                        width: 160px;
                    }
                }
            }
        }
    }

    &.v-3{
        .banner-beneficios__item{
            &.is-top{
                @media screen and (min-width: $bp-XL){
                    margin-top: 37px;
                }
            }
        }

        .banner-beneficios__caption{
            .title{
                font-size: 33px;
                padding-left: 30px;
                letter-spacing: 5px;
                margin-right: -200px;

                @media screen and (max-width: 1202px){
                    font-size: 25px;
                    padding-left: 0;
                    margin-left: -40px;
                    margin-top: 5px;
                }

                @media screen and (max-width: $bp-LG){
                    margin-left: 0;
                }
            }

            @keyframes slideIn300px {
                0% {
                    max-width: 0;
                }
                100% {
                    max-width: 300px;
                }
            }

            .has-line{
                .line{
                    right: 11px;
                    animation: slideIn300px 1s;
                    animation-fill-mode: forwards;
                    width: 150px;

                    @media screen and (max-width: 1270px){
                        width: 90px;
                    }

                    @media screen and (max-width: $bp-MD){
                        width: 40px;
                    }
                }
            }
        }
    }

    &.v-4{
        .banner-beneficios__item{
                @media screen and (min-width: $bp-XL){
                    margin-top: 21px;
                }
            
        }

        .banner-beneficios__caption{
            .title{
                font-size: 36px;
                padding-left: 30px;
                letter-spacing: 5px;
                margin-right: -200px;

                @media screen and (max-width: 1202px){
                    font-size: 25px;
                    padding-left: 0;
                    margin-left: -40px;
                    margin-top: 5px;
                }

                @media screen and (max-width: $bp-LG){
                    margin-left: 0;
                }
            }

            @keyframes slideIn300px {
                0% {
                    max-width: 0;
                }
                100% {
                    max-width: 300px;
                }
            }

            .has-line{
                .line{
                    right: 11px;
                    animation: slideIn300px 1s;
                    animation-fill-mode: forwards;
                    width: 290px;

                    @media screen and (max-width: 1270px){
                        width: 90px;
                    }

                    @media screen and (max-width: $bp-MD){
                        width: 110px;
                        right: 6px;
                    }
                }
            }
        }
    }

    &.v-5{
        .banner-beneficios__item{
                @media screen and (min-width: $bp-XL){
                    margin-top: 21px;

                    &.is-top{
                        margin-top: 45px;
                    }
                }
        }

        .banner-beneficios__caption{
            .title{
                font-size: 40px;
                padding-left: 50px;
                letter-spacing: 5px;
                margin-right: -200px;

                @media screen and (max-width: 1202px){
                    font-size: 35px;
                    padding-left: 0;
                    margin-left: -40px;
                    margin-top: 5px;
                }

                @media screen and (max-width: $bp-LG){
                    margin-left: 0;
                }
            }

            @keyframes slideIn300px {
                0% {
                    max-width: 0;
                }
                100% {
                    max-width: 300px;
                }
            }

            .has-line{
                .line{
                    right: 11px;
                    animation: slideIn300px 1s;
                    animation-fill-mode: forwards;
                    width: 230px;

                    @media screen and (max-width: 1270px){
                        width: 150px;
                    }

                    @media screen and (max-width: $bp-MD){
                        width: 80px;
                        right: 9px;
                    }
                }
            }
        }
    }
}
